import React  from 'react';
import Header from "./Header";
import Box from "./box";

const MainNav = () => {
  return (
    <div className="right-container" style ={{height:'572px'}}>
      <div className="top-header">
        <Header />
      </div>
      <div className="weather-container">
        <Box />
      </div>
    </div>
  );
};

export default MainNav;



