import React from 'react';
import "./styles.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBarsProgress,
  faDroplet,
  faRainbow,
  faRulerHorizontal,
  faSliders,
  faSnowflake,
  faSun,
  faTemperature0,
  faThermometer,
  faWater
} from "@fortawesome/free-solid-svg-icons";
import { faWind } from "@fortawesome/free-solid-svg-icons";
import { faGauge } from "@fortawesome/free-solid-svg-icons";
import { faCloudRain } from "@fortawesome/free-solid-svg-icons";

const rowStyle1 = {
  display: "flex",
  flexFlow: "wrap",
  justifyContent: "center",
  verticalAlign: "middle",
  marginTop: "15px",
  marginLeft: "20px"
};
const rowStyle2 = {
  display: "flex",
  flexFlow: "wrap",
  justifyContent: "center",
  verticalAlign: "middle",
  marginTop: "15px"
};
const iconStyle = {
  color: "white",
  backgroundColor: "#5c9be2",
  borderRadius: "15%",
  marginLeft: "70px",
  marginRight: "30px",
  fontSize: "25px",
  marginTop: "-90em"
};

const listStyle = {
  listStyleType: "None",
  display: "inline",
  fontSize: "15px",
  padding: "17px"
};
const h2Style2 = {
  textAlign: "center",
  marginTop: "-10px"
};
const h2Style1 = {
  marginTop: "1px",
  padding: "10px"
};
const iSytle2 = {
  fontSize: "30px",
  marginLeft: "90px",
  marginTop: "-10px"
};

const spanStyle = {

}

const Box = () => {
  const weatherStatus = [{
    name: 'Good',
    count: 100
  },
  {
    name: 'Noraml',
    count: 80
  },
  {
    name: 'Bad',
    count: 20
  }]
  const feels = [
    {
      "name": "0%",
      "count": 60
    },
    {
      "name": "25%",
      "count": 60
    },
    {
      "name": "50%",
      "count": 60
    },
    {
      "name": "75%",
      "count": 60
    },
    {		
      "name": "100%",
      "count": 60
    }
  ]
  const uvindex = [
    {
      "name": "0 - 2",
      "count": 100
    },
    {
      "name": "3 - 5",
      "count": 60
    },
    {
      "name": "6 - 7",
      "count": 0
    },
    {
      "name": "8 - 10",
      "count": 0
    },
    {
      "name": "11+",
      "count": 0
    }
  ]
  const precipitation = [
    {
      "name": "0",
      "count": 100
    },
    {
      "name": "10",
      "count": 90
    },
    {
      "name": "20",
      "count": 80
    },
    {
      "name": "30",
      "count": 0
    },
    {
      "name": "40",
      "count": 0
    },
    

  ]
  const w = [
    {
      "name": "0",
      "count": 60
    },]
  return (
    
      <div className="current-weather">
        <h4>More details of today's weather</h4>
        <div className="row-section">
          <div className="individual-weather">
            <div className="weather-details">
              <div className="weather-title">
                <h2 >Humidity</h2>
              </div>

              <div className="weather-icon">
                <FontAwesomeIcon icon={faDroplet} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>87% Bad</h2>
            <div className="weather-status">
              {weatherStatus.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
          </div>

          <div className="individual-weather">
            <div className="weather-details">
              <div className="weather-title">
                <h2>Feel Like</h2>
              </div>
              <div className="weather-icon">
                <FontAwesomeIcon icon={faRainbow} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>42%</h2>

            <div className="weather-status">
              {feels.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
            

          </div>

          <div className="individual-weather">
            <div className="weather-details">
              <div className="weather-title">
                <h2>Precipitation</h2>
              </div>
              <div className="weather-icon">
                <FontAwesomeIcon icon={faCloudRain} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>1.4 Cm</h2>
            <div className="weather-status">
              {precipitation.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
          </div>
        </div>

        <div className="row-section">
          <div className="individual-weather">
            <div className="weather-details">
              <div className="weather-title">
                <h2>UV Index</h2>
              </div>
              <div className="weather-icon">
                <FontAwesomeIcon icon={faSun} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>
              4 <i>medium</i>
            </h2>
            <div className="weather-status">
              {uvindex.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
          </div>

          <div className="individual-weather">
            <div className="weather-details">

              <div className="weather-title">
                <h2>Weather</h2>
              </div>
              <div className="weather-icon">
                <FontAwesomeIcon icon={faTemperature0} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>-15&#176;</h2>
            <div className="weather-status">
              {w.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
          </div>

          <div className="individual-weather">
            <div className="weather-details">
              <div className="weather-title">
                <h2>Wind</h2>
              </div>
              <div className="weather-icon">
                <FontAwesomeIcon icon={faWind} className="weather-fa-icon" />
              </div>
            </div>
            <h2 style={{ textAlign: 'center', marginTop: "-10px" }}>8 Km/h</h2>
            <div className="weather-status">
              {weatherStatus.map((item, index) => (

                <div className="status-info" key={index}>
                  <div className="status-title">
                    {item.name}
                    <div className="progress-bar">
                      <div className="progress-status" style={{ width: `${item.count}%` }}></div>
                    </div>
                  </div>
                </div>
              )
              )}
            </div>
          </div>

        </div>

      </div>
    
  );
};

export default Box;



