import React  from 'react';
import tower from "./imahes/tower.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGreaterThan, faSun, faPaperPlane, faLessThan } from "@fortawesome/free-solid-svg-icons";

const div3Style = {
  display: "flex",
  marginLeft: "50px",
  textAlign: "center",
  color: "#FFFFFF",
  fontSize: "30px"
};
const div2Style = {
  color: "white",
  marginLeft: "50px",
  fontFamily: "Arial"
};
const imgStyle = {
  marginTop: "5%",
  marginBottom: "-3.5px",

  borderBottomLeftRadius: "5%"
};
const div1Style = {
  backgroundColor: "#5c9be2",
  width: "30%",
  borderTopLeftRadius: "60px",
  borderBottomLeftRadius: "60px",
};
const SideNav = () => {
  return (
    <div style={div1Style}>
      <div style={div2Style}>
        <span style={{ display: "flex", marginTop: "2em" }}>
          <h3>
            <FontAwesomeIcon icon={faPaperPlane} />
          </h3>
          <h3 style={{ width: "50%", marginLeft: "7px" }}>
            <b>Chennai INDIA</b>
          </h3>
          <h3>
            <FontAwesomeIcon icon={faSun} style={{ color: "#FFFFFF" }} />
          </h3>
          <h3 style={{ width: "50%" }}>07:32</h3>
        </span>
        <span style={{ display: "flex", marginTop: "-2em" }}>
          <h3 style={{ width: "50%", marginLeft: "30px" }}>
            <b>Today 30 June</b>
          </h3>
          <h3>
            <FontAwesomeIcon icon={faSun} style={{ color: "#FFFFFF" }} />
          </h3>
          <h3 style={{ width: "50%" }}>17.32</h3>
        </span>
      </div>
      <div style={div3Style}>
        <div style={{ marginRight: "50px", fontSize: "14px", paddingTop: "70px" }}>
          <FontAwesomeIcon icon={faLessThan} />
        </div>
        <h1>27&#176;</h1>
        <div style={{ marginLeft: "50px", fontSize: "14px", paddingTop: "70px" }}>
          <FontAwesomeIcon icon={faGreaterThan} />
        </div>
      </div>
      <div style={imgStyle}>
        <img src={tower} />
      </div>
    </div>
  );
};
export default SideNav;



